.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}



.card {
  width: 98.8vw;
  margin: auto;
  height: 100vh;
  background: rgba(0, 0, 255, 0.275);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  top: 0;
}

.card img {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;

}

.card-content {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}



.aminitiescontainer {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .aminitiescontainer {
    grid-template-columns: repeat(2, 1fr);
    overflow-y: scroll;
  }
}


@media screen and (max-width: 480px) {
  .aminitiescontainer {
    grid-template-columns: repeat(2, 1fr);
    overflow-y: scroll;
  }
}



.type_writer {
  display: inline-block;
  width: 20rem;
}

.type_writer p {
  font-size: 1.5rem;
  letter-spacing: 0.3rem;
  font-family: sans-serif;
  border-right: 0.1rem solid;
  width: 100%;
  height: 15rem;
  overflow: hidden;
  animation: typing 2s,
    cursor .4s step-end 5 alternate;
}


@keyframes cursor {
  50% {
    border-color: transparent;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}